import React from 'react';
import {inject} from "mobx-react";
import moment from "moment";
import { BetEvent } from '@web3game/typings/openBox';
import {Popup} from "./popups/Popup";
import {HistoryStore} from "../../openBox/stores/HistoryStore";
import {Table} from "./Table";


type HistoryListProps = {
    onPlayGame: (betEvent: BetEvent) => void;
    displayList?: [];
};

export function HistoryListPure(
    {
        onPlayGame,
        displayList,
    }: HistoryListProps,
) {
    const tHeader = (
        <tr>
            <th className="historyNo">期号</th>
            <th>金额</th>
            <th className="historyDate">日期</th>
            <th className="historyAction">{" "}</th>
        </tr>
    );

    const renderListItem = React.useCallback(
        (item: any) => (
            <tr key={item.id}>
                <td className="historyNo">{item.issueNo}</td>
                <td>{item.money}</td>
                <td className="historyDate">{moment(item.betTime).format('MM/DD HH:mm')}</td>
                <td className="historyAction">
                    <button
                        className="historyActionButton"
                        onClick={() => onPlayGame(item)}
                        type="button"
                    />
                </td>
            </tr>
        ),
        [],
    );

    const listContent = displayList ? displayList.map(renderListItem) : null;

    return (
        <Popup title="投注记录">
            <Table
                tHead={tHeader}
                className="historyList"
                bodyClassName="purchaseListTbody"
            >
                {listContent}
            </Table>
        </Popup>
    );
}

type BoxGridInjectedStores = {
    historyStore?: HistoryStore;
};

export const HistoryListPopup = inject(
    ({ historyStore }: BoxGridInjectedStores) => ({
        displayList: historyStore ? historyStore.displayList : [],
    }),
)(HistoryListPure);
